
.App {
  display: flex;
  flex-direction: column;
  min-width: 20em;
  max-width: 28em;
  margin: auto;
}

.title-bar {
  display: flex;
  justify-content: space-between;
  background-color: #3f51b5;
  color: #fff;
  height: 2em;
  font-size: 2em;
  padding: 0.5em;
}

.item {
  display: flex;
  justify-content: space-between;
  /* text-align: center; */
  border:1px #ccc solid;
  font-size: 1em;
  padding: 1.2em;
}

.item-text {
  display: flex;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 16em;
}

.item-done {
  display: flex;
  justify-content: space-between;
  text-align: center;
  border:1px #ccc solid;
  background-color: darkgrey;
  font-size: 1em;
  padding: 1.2em;
  text-decoration: line-through;
}

.edit-bar {
  width: 4em;
  height: 1.6em;
}

.task-list-button {
  width: 1.3em;
  height: 1.3em;
}

.task-list-button:hover {
  width: 1.6em;
  height: 1.6em;
}

.task-edit-button {
  float: left;
  /* margin-right: 1.5em; */
  width: 1.3em;
  height: 1.3em;
}

.task-edit-button:hover {
  width: 1.6em;
  height: 1.6em;
}

.task-clear-button {
  float: right;
  width: 1.3em;
  height: 1.3em;
}

.task-clear-button:hover {
  width: 1.6em;
  height: 1.6em;
}

.input-item {
  border:1px #ccc solid;
  font-size: 1em;
  padding: 1.2em;
}

.input-item:hover {
  color: #212121;
  border-bottom: 3px #3f51b5 solid;
 }